.iconInput{
  display:flex;
  height:auto;
  padding:0.5em;
  background-color:white;
  border-radius:10px;
  flex-direction:row-reverse;
  width:90%;
  margin:0.25em auto;
}

.iconInput>input{
  background-color:transparent;
  box-shadow:unset;
  flex:1 0 auto;
}

.iconInput i{
  color:black;
  margin:auto;
}

.iconInput>div{
  flex:0.5 0 0;
}
