.toolTip{
    position:relative;
}

.toolTip .toolTipText{
    --wd:120px;
    --pd:5px;
    position:absolute;
    z-index:3;
    transition:opacity 0.1s;
    margin-left:calc( (var(--wd)/2) * -1);
    opacity:0;
    width:var(--wd);
    padding:var(--pd) 0;
    background-color:black;
    color:white;
    font-weight:bold;
    height:auto;
    word-wrap:break-word;
    text-align:center;
    border-radius:10px;
}

.toolTip .toolTipText::after{
    content:"";
    position:absolute;
    margin-left:-5px;
    border-style:solid;
    border-color:black transparent transparent transparent;
}

.toolTip .top{
    bottom:100%;
    left:50%;
}

.toolTip .bottom{
    top:100%;
    left:50%;
}

.toolTip .left{
    top:50%;
    left:75%;
}

.toolTip .right{
    top:50%;
    right:75%;
}
 
.toolTip .top::after{
    top:100%;
    left:50%;
}

.toolTip:hover .toolTipText{
    opacity:1;
}

.toolTip .left::after{
    top:50%;
    right:100%;
    border-color:transparent black transparent transparent;
}

.toolTip .right::after{
    top:50%;
    left:100%;
    border-color:transparent transparent transparent black;
}

.toolTip .bottom::after{
    bottom:100%;
    left:50%;
    border-color:transparent transparent black transparent;
}
