.marquee{
  --bg-color:white;
  --color:black;
  --speed:15s;
  width:100%;
  height:100%;
  background-color:var(--bg-color);
  overflow:hidden;
}

.marquee .content{
  color:var(--color);
  display:inline-block;
  padding-left:100%;
  animation: marqueeFwd var(--speed) linear infinite;
}

.reverse{
  animation-name: marqueeRev;
}

@keyframes marqueeFwd{
  0%{
    transform: translate(0, 0);
  }
  100%{
    transform: translate(-100%, 0);
  }
}

@keyframes marqueeRev{
  0%{
    transform: translate(-100%, 0);
  }
  100%{
    transform: translate(0%, 0);
  }
}
