#main .fab{
  --color:var(--pri);
}

.blogCard{
  background-color:#c4c4c4;
}

#blogContainer{
  display:flex;
  flex-direction:column;
  align-items:center;
}

#blogContainer>img{
  width:90%;
  height:90%;
}

#blogContainer .blog{
  margin:0.5em;
}

#videoContainer{
  display:flex;
  flex-direction:column;
}

#videoContainer:first-child{
  flex:0.2;
}

#videoContainer:last-child{
  flex:0.8;
}

.videoBox{
  display:flex;
  flex-flow:row;
  justify-content:space-around;
}

.videoBox>div{
  margin:0.5em;
}

.videoBox>img{
  width:90%;
  height:90%;
}

:global(.dark) #main .fab *{
  color:black;
}

@media screen and (max-width:1120px){
  #blogContainer{
    justify-content:space-around;
  }
}
