.overlay{
  opacity:0;
  display:none;
  background-color:rgba(0,0,0,0.5);
  width:100%;
  height:100%;
  position:absolute;
  top:0;
  left:0;
  z-index:99;
}

.show{
  opacity:1;
  display:block;
  cursor:not-allowed;
}

.overlay .loaderContainer{
  position:relative;
  width:100%;
  height:100%;
}

.overlay .loaderContainer .loader{
  --size:120px;
  --bord:16px;
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  border-radius:50%;
  margin:auto;
  width:var(--size);
  height:var(--size);
  border:var(--bord) solid black;
  border-top:var(--bord) solid white;
  animation: spin 1s ease-in-out infinite forwards;
}

@keyframes spin{
  from{
    transform:rotate(0deg);
  }
  to{
    transform:rotate(360deg);
  }
}
