.notificationContainer{
  --margin:10px;
  position:absolute;
  bottom:var(--margin);
  right:var(--margin);
  top:0;
  height:100%;
  z-index:99;
  display:grid;
  grid-template-rows:auto var(--fr);
  grid-template-columns:var(--fr);
  grid-gap:5px;
}

.notificationBox{
  display:flex;
  flex-direction:column-reverse;
  overflow:hidden;
  overflow-y:auto;
  height:100%;
  width:100%;
}

.notificationContainer .close > *{
  justify-content:flex-end;
}

.notificationContainer .close *{
  color:red;
  font-size:2rem;
}
