#navbar {
    --height: 45px;
    width: 100%;
    height: var(--height);
    display: grid;
    grid-template-columns: var(--icon-size) var(--fr) 60px;
    grid-template-rows: var(--fr);
    grid-gap: 10px;
    background-color: var(--pri);
}

#navbar>* svg{
	fill:var(--logo-color);
}

:global(.dark) #navbar{
    background-color:var(--pri) !important;
}
