.iconWrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.iconWrapper:hover {
    cursor: pointer;
}

.iconWrapper>div {
    margin: 0 0.15em;
    display: flex;
    align-items: center;
    justify-content:center;
}

.icon {
    width: var(--icon-size);
    height: var(--icon-size);
}

.icon img {
    width: inherit;
    height: 100%;
}

.iconTitle {
    font-weight: bold;
}

.icon svg{
  width:100%;
}

@media screen and (max-width:300px){
    .iconTitle{
        display:none !important;
    }
}
