#newAndOldBlog{
  display:grid;
  grid-template-columns:var(--fr);
  grid-template-rows:var(--fr) auto 0.2fr;
  grid-gap:5px;
}

#postContainer{
}

#postContainer .genreSelect{
  display:flex;
  background-color:white;
  width:90%;
  height:auto;
  justify-content:center;
  align-items:center;
  border-radius:10px;
  padding:0.5em;
}

#postContainer .genreSelect *{
  color:black;
}

#postContainer .genreSelect .title{
  font-weight:bolder;
  flex:0.1 0;
}

#postContainer .genreSelect select{
  flex:1 0;
  padding:0.5em;
}

#newAndOldBlog .videoChips>*, #newAndOldBlog .blogList>*{
  margin: 0.5em;
}
