.tgSwitch, .tgSwitch .slider::before{
    --hg: 10px;
    --wd: 30px;
}

.tgSwitch {
    position: relative;
    display: inline-block;
    width: var(--wd);
    height: var(--hg);
    background-color: #000;
    border-radius:24px;
}

.tgSwitch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.tgSwitch .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    border-radius: 25px;
}

.tgSwitch .slider::before {
    --s-wd: calc(var(--wd) /3);
    position: absolute;
    content: "";
    height: var(--hg);
    width: var(--s-wd);
    left: 0;
    border-radius:50%;
    background-color:white;
    transition:transform 0.4s linear;
}

.tgSwitch input:checked+.slider::before {
    transform: translateX(calc(var(--wd) - var(--s-wd)));
}

.tgSwitch .active::before{
    background-color:#5dd2ff;
}
