.container{
  display:grid;
  grid-template-rows: auto var(--fr);
  grid-template-columns:var(--fr);
  overflow:hidden;
  grid-gap:10px;
  height:100%;
  width:100%;
}

.selectionContainer{
  display:flex;
  justify-content:center;
  align-items:center;
  margin:0.25em 0;
}

.selectionContainer > *{
  margin:0 0.5em;
  color:black;
}

.selectionContainer .active{
  font-weight:bold;
}
