.fab{
  --mg: 20px;
  --size: 50px;
  --color:red;
  --menu-item-color:darkslategray;
  --menu-bg:lightgray;
  position: fixed;
  right: 0;
  display: flex;
  flex-direction: column-reverse;
  margin: var(--mg);
  bottom: 0;
  border-radius: 50%;
  width: var(--size);
  height: var(--size);
  background-color: var(--color);
  font-weight: bold;
  text-align: center;
  display: flex;
  justify-content: center;
  box-shadow: 5px 0 10px rgba(var(--color), 0.8);
  cursor: pointer;
  z-index: var(--fab-z);
}

.fabWithMenu .title{
  flex: 1 0 100%;
}

.fabWithMenu .fabMenu{
  position: relative;
  flex: 1 0 auto;
}

.fabWithMenu .fabMenu .menuContainer{
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 0.5em;
  margin: var(--mg) 0;
  width: auto;
  height: auto;
  background-color: var(--menu-bg);
  display: flex;
  flex-direction: column;
  transform: scaleY(0);
  border-radius:10px;
}

.fabWithMenu .fabMenu .menuContainer>*{
  position:relative;
  cursor:pointer;
}

.fabWithMenu .fabMenu .menuContainer .anchor{
  text-decoration:none;
  width:100%;
  height:100%;
}


.fabWithMenu .fabMenu .menuContainer > *::after{
  position:absolute;
  bottom:0;
  right:0;
  border:2px solid black;
  content:"";
  width:100%;
  height:auto;
}

.fabWithMenu .fabMenu .menuContainer *{
  color:var(--menu-item-color);
}

.show .fabMenu .menuContainer{
  transform: scaleY(1);
}

.show.title{
  transform: rotate(45deg);
  color: white;
}
