#authContainer{
  display:grid;
  grid-template-rows: auto var(--fr);
  grid-template-columns:var(--fr);
  overflow:hidden;
  grid-gap:10px;
}

.authSelector{
  display:flex;
  justify-content:center;
  align-items:center;
  margin:0.25em 0;
}

.authSelector > *{
  margin:0 0.5em;
  color:black;
}

.authSelector .active{
  font-weight:bold;
}
/*Login and signup*/
#loginForm, #signupForm{
  width:100%;
  height:100%;
  display:grid;
  grid-template-columns:var(--fr);
  grid-template-rows:0.25fr var(--fr);
}

.formContainer{
  background-color:var(--logo-color);
}
