#profileContainer{
  width:100%;
  height:100%;
  position:relative;
}

#profileCard{
  display:grid;
  width:90%;
  height:90%;
  margin: 0.5em auto;
  border:2px solid black;
  border-radius:10px;
  grid-template-rows:var(--fr) auto auto auto auto;
  grid-template-columns:var(--fr);
  grid-gap:10px;
  position:relative;
  background-color:white;
}

#profileCard *{
  cursor:default;
}

#profileCard .avatar{
  width:100%;
  height:100%;
  display:flex;
  position:relative;
  overflow:hidden;
}

#profileCard .avatar .avatarBg{
  background-size:cover;
  position:absolute;
  width:100%;
  height:100%;
  top:0;
  left:0;
  opacity:0.5;
  filter:brightness(30%);
  background-position:center;
}

#profileCard .avatar .avatarBg:hover{
  transform:scale(1.5);
}

#profileCard .avatar img{
  --size:100px;
  width:var(--size);
  height:var(--size);
  margin:auto;
}

#profileCard .empty, #profileCard .empty *{
  color:red
}

#profileCard .yes, #profileCard .yes *{
  --clr:#000eff;
  background-color:var(--clr);
}

#profileCard .no, #profileCard .no *{
  --clr:#f3983a;
  background-color:var(--clr);
}

#profileCard .emailVerified{
  --mg:10px;
  top:var(--mg);
  right:var(--mg);
  position:absolute;
  z-index:1;
}
