#sidemenu {
    width: 100%;
    height: 100%;
    position: relative;
}

#sidemenu .anchor{
  text-decoration:none;
  outline:none;
}

.barContainer {
    width: 100%;
    height: 100%;
    display: inline-block;
    cursor: pointer;
}

.active .bar {
    background-color: red !important;
}

.active .bar:first-child {
    transform: rotate(-45deg) translate(-9px, 6px);
}

.active .bar:nth-child(2) {
    opacity: 0;
}

.active .bar:last-child {
    transform: rotate(45deg) translate(-8px, -8px);
}

.bar {
    --bar-hg: 5px;
    width: 100%;
    margin: 6px 0;
    transition-timing-function: ease-in;
    height: var(--bar-hg);
    background-color: black;
    border-radius: 10px;
    background-color:white;
}

.overlayMenu {
    position: fixed;
    left: 0;
    transition-timing-function: linear;
    width: 0px;
    height: 100%;
    z-index: -1;
    background-color: var(--sec);
    overflow-y: auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    top:0;
}

.active .overlayMenu {
    width: 250px;
    z-index: var(--sideMenu-z);
}

.overlayMenu div {
    height: 60px;
    cursor:pointer;
}

.close{
	display:flex;
	justify-content:flex-end;
	align-items:center;
}

.close i{
	color:red;
}

@media screen and (max-width:600px) {
    .active .overlayMenu {
        width: 100%;
    }
}

/*theme*/
.overlayMenu *{
	color:black;
}
