.formContainer{
  width:100%;
  height:100%;
  display:grid;
  grid-template-columns:var(--fr);
  grid-template-rows:0.25fr var(--fr);
}

.formContainer .head{
  width:100%;
  height:100%;
  text-align:center;
  display:flex;
  flex-direction:column;
  justify-content:center;
}

.formContainer .head > div, .formContainer .head .toolTip>div:first-child{
  color:black;
}

.formContainer .head .title{
  font-weight:bolder;
  font-size:7vmax;
}

.formContainer .head .miniTitle{
  font-size:3vmax;
  font-weight:bold;
}

.formContainer .form{
  display:flex;
  justify-content:center;
  flex-direction:column;
}

.formContainer .inputContainer{
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
}

.formContainer .buttonContainer{
  display:flex;
  justify-content:center;
  align-items:center;
}

:global(.dark) .formContainer .head >div,
:global(.dark) .formContainer .head .toolTip>div:first-child{
  color:white;
}
