.button{
  margin:0.5em;
  text-select:none;
  padding: 0.5em;
  border: none;
  border-radius: 10px;
  min-width: 100px;
  min-height: 35px;
  font-weight: bold;
  cursor:pointer;
  outline:none;
  color:black;
  background-color:lightgray;
  position:relative;
  overflow:hidden;
}

.ripple{
  --bg:lightgray;
  background-position:center;
  transition: background 0.6s;
}

.ripple:hover{
  background: var(--bg) radial-gradient(circle, transparent 1%, rgba(0,0,0,0.2) 1%) center/15000%;
}

.ripple:active{
  background-size: 100%;
  transition: background 0s;
}
