.notification{
  --wd:250px;
  --hg:100px;
  --bg:black;
  display:grid;
  width:var(--wd);
  height:var(--hg);
  grid-template-rows:0.2fr var(--fr);
  grid-template-columns:var(--fr);
  background-color:var(--bg);
  border-radius:15px;
  padding:0.5em;
  margin:0.5em 0;
  grid-gap:10px;
  animation: fromBottom 1s ease-out forwards;
}

.minimized{
  grid-template-rows:auto;
  height:auto;
}

.minimized .content{
  display:none;
}

.menu{
  display:flex;
}

.menu .title{
  flex:1 0 auto;
  text-align:center;
  font-weight:bolder;
  text-transform:capitalize;
  width:50%;
  overflow:hidden;
  white-space:nowrap;
  text-overflow:ellipsis;
}

.menu .options{
  display:flex;
}

.menu .options .opt{
  --size:calc(var(--hg) * 0.2);
  width:var(--size);
  height:var(--size);
  border-radius:50%;
  color:black;
  display:flex;
  justify-content:center;
  align-items:center;
  cursor:pointer;
}

.menu .options .opt:hover{
  opacity:0.7;
}

.menu .options #close{
  background-color:red;
}

.menu .options #min{
  background-color:yellow;
}

.notification .content{
  overflow-y:auto;
  overflow-x:hidden;
}

.notification .content >*{
  text-align:justify;
  word-break:break-word;
}

@keyframes fromBottom{
  from{
    transform:translateY(100px);
    opacity:0;
  }
  to{
    transform:translateY(0px);
    opacity:1;
  }
}

:global(.dark) .notification{
  --bg:lightgray;
}
