.blog{
  --size:400px;
  --bg:gray;
  --title-bg:lightgray;
  --details-bg:white;
  display:grid;
  width:90%;
  height:var(--size);
  background-color:var(--bg);
  box-shadow: 10px 10px 10px  rgba(0,0,0,0.7);
  border-radius:10px;
  text-align:center;
  cursor:pointer;
  grid-template-columns:minmax(0, 0.6fr) minmax(0,0.4fr);
  grid-template-rows:var(--fr);
}

.blog *{
  user-select:none;
  color:black;
}

.blog .titleAssetContainer>div:first-child{
  border-radius:10px;
  border-top-right-radius:unset;
  border-bottom-right-radius:unset;
  border-bottom-left-radius:unset;
}

.blog>div:last-child{
  border-radius:10px;
  border-top-left-radius:unset;
  border-bottom-left-radius:unset;
}

.blog .titleAssetContainer{
  flex:0.5;
  display:flex;
  flex-direction:column;
  width:100%;
  height:100%;
}

.blog .titleAssetContainer .title{
  font-size:5vmax;
  font-weight:bolder;
  text-align:center;
  background-color:var(--title-bg);
}

.blog .titleAssetContainer .assets{
  display:block;
  overflow:hidden;
  scroll-behavior:smooth;
  overflow-x:auto;
  white-space:nowrap;
  height:100%;
  scroll-snap-type:x mandatory;
}

.blog .titleAssetContainer .assets .asset{
  width:100%;
  height:100%;
  scroll-snap-align:center;
  display:inline-block;
  position:relative;
  z-index:1;
}

.blog .titleAssetContainer .assets .asset .bg{
  background-repeat:no-repeat;
  background-size:cover;
  filter:blur(10px) brightness(70%);
  position:absolute;
  width:100%;
  height:100%;
  z-index:-1;
  top:0;
  left:0;
}

.blog .titleAssetContainer .assets .asset > *{
  object-fit:contain;
  height:100%;
  border-radius:10px;
}

.blog .details{
  background-color:var(--details-bg);
  width:100%;
  height:100%;
  overflow:hidden;
  display:grid;
  grid-template-columns:var(--fr);
  grid-template-rows:auto var(--fr);
}

.blog .details .meta{
  display:grid;
  grid-template-columns:auto auto var(--fr);
  grid-template-rows:var(--fr);
  background-color:var(--details-bg);
  align-items:center;
  height:auto;
  flex:0.1 1;
}

.blog .details .meta > *{
  margin:0 0.25em;
  cursor:pointer;
}

.blog .details .meta :global(.fa-heart){
  color:red;
}

.blog .details .meta :global(.fa-comment){
  color:lightblue;
}

.blog .details .meta .blogMeta{
  overflow:hidden;
  white-space:nowrap;
}

.blog .details .meta .blogMeta .auth{
  display:inline-block;
  font-weight:bolder;
  border-bottom: 5px solid black;
}

.blog .details .content{
  overflow:hidden;
  overflow-y:auto;
}

.blog .commentContainer{
   display:flex;
   flex-direction:column;
   width:100%;
   height:100%;
}

.blog .commentContainer >.comments{
  flex: 1 0;
  overflow:hidden;
  overflow-y:auto;
}

.blog .commentContainer>div:not(.comments){
  flex:0.1 0;
  background-color:lightgray;
}

.blog .commentContainer .comments .commentBox{
  height:auto;
  margin:0.5em;
  margin-left:auto;
  padding:0.4em;
  display:table;
  border-radius:10px;
  background-color:lightgray;
}

.blog .commentContainer .comments .comment{
  font-weight:bolder;
  text-align:right;
  word-break:break-word;
}

.blog .commentContainer .comments .commentee{
  font-size:0.5em;
  text-align:right;
}

@media screen and (max-width:600px){
  .blog{
    grid-template-rows: minmax(0, 0.6fr) minmax(0,0.4fr);
    grid-template-columns: var(--fr)
  }
  .blog .titleAssetContainer>div:first-child{
    border-radius:10px;
    border-bottom-right-radius:unset;
    border-bottom-left-radius:unset;
  }

  .blog>div:last-child{
    border-radius:10px;
    border-top-left-radius:unset;
    border-top-right-radius:unset;
  }

}
