:root {
    --icon-size: 35px;
    --txt:#949494;
    --sec:#f0eff4;
    --pri:#291528;
    --d-pri:#9e829c;
    --d-sec:#3a3e3b;
    --main-z:2;
    --notification-z:4;
    --sideMenu-z:5;
    --fab-z:3;
    --fr:minmax(0,1fr);
}

* {
    margin: 0;
    padding: 0;
    transition: all 0.2s linear;
    color:var(--txt);
    box-sizing: border-box;
}

#anime_world {
    width: 100%;
    min-height: 100vh;
    display:grid;
    position:relative;
    grid-template-rows:auto var(--fr);
    grid-template-columns:var(--fr);
    background-color:var(--sec);
}

.animeContainer {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    justify-content: space-around;
}

.sideMenuOpen::before{
	cursor:default;
	width:100%;
	height:100%;
	position:absolute;
	content:"";
	top:0;
	left:0;
	z-index:var(--main-z);
	background-color:rgba(0,0,0,0.6);
}

#anime_world .notificationContainer{
  --navbar-hg:45px;
  top:var(--navbar-hg);
  z-index:var(--notification-z);
  bottom:0;
  right:0;
  background-color:rgba(0,0,0,0.5);
  height: calc(100% - var(--navbar-hg));
}

:global(.dark){
    --pri:var(--d-pri);
    --sec:var(--d-sec);
}

:global(.dark) *{
	--txt:#010001;
}
